// ========================================

class MenuGame extends Component {
    render() {
        return (
            <div  className="flex-row flex-align-start ">
                <div id="commandsDiv" className="overflow overflow-y-visible margin-10">
                    <GameTable />
                </div>
                <div >
                    <GameButtonsTable />
                </div>
            </div>
        )
    }
}

class GameTable extends Component{
    constructor(props) {
        super(props);
        this.state = {
            rows: [(<GameTableRow index={1} onClick={() => this.handleClick()} />)],
        };
    }

    handleClick(){
        const rows = this.state.rows.slice();
        this.setState({rows: rows.concat([
                (<GameTableRow index={rows.length+1} onClick={() => this.handleClick()} />)
            ])})
    }

    render() {
        return (
            <table id="commands">
                <tr>
                    <th>#</th>
                    <th>Наводчик</th>
                    <th>Водитель</th>
                    <th>Заряжающий</th>
                </tr>
                {this.state.rows}
            </table>
        )
    }
}

class GameTableRow extends Component{
    constructor(props) {
        super(props);
        this.state = {
            index: props.index,
            onClick: props.onClick,
        };
    }

    render() {
        return (
            <tr>
               <td style={{width: 20}} ><button onClick={() => this.state.onClick()}>{this.state.index} </button></td>
               <GameTableCell />
               <GameTableCell />
               <GameTableCell />
            </tr>
        );
    }
}

class GameTableCell extends Component{
    constructor(props) {
        super(props);
        this.state = {
            value: null,
        };
    }

    render() {
        return (
            <td className="gameTD">
                {this.state.value}
            </td>
        );
    }
}

// ========================================

class GameButtonsTable extends Component{
    constructor(props){
        super(props);

        this.state={
            gunnerSelectedIndex: 0,
            driverSelectedIndex: 0,
            chargerSelectedIndex: 0
        }
    }

    renderButton(className,selected, index, position){
        return <GameButton index={index} class={className} selected={selected} onClick={() => this.handleClick(index, className)} position={position} />
    }

    handleClick(index, className){
        this.setState({
            gunnerSelectedIndex: className === "gunner" ? index : this.state.gunnerSelectedIndex,
            driverSelectedIndex: className === "driver" ? index : this.state.driverSelectedIndex,
            chargerSelectedIndex: className === "charger" ? index : this.state.chargerSelectedIndex
        })
    }

    render() {
        return (
            <div className="flex-row flex-align-start overflow">
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Наводчик</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 0 , 0, "0px -150px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 1 , 1, "-50px 0px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 2 , 2, "-100px 0px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 3 , 3, "0px -50px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 4 , 4, "-50px -50px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 5 , 5, "-100px -50px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 6 , 6, "0px -100px")}
                        {this.renderButton("gunner", this.state.gunnerSelectedIndex === 7 , 7, "-100px -100px")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Водитель</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButton("driver", this.state.driverSelectedIndex === 0, 0, "0px 0px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 1, 1, "-50px -150px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 2, 2, "-100px -150px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 3, 3, "0px -200px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 4, 4, "-50px -200px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 5, 5, "-100px -200px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 6, 6, "-50px -100px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 7, 7, "-150px 0px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 8, 8, "-150px 0px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 9, 9,  "-150px 0px")}
                        {this.renderButton("driver", this.state.driverSelectedIndex === 10,10, "-150px 0px")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20, }} className="padding-5">Заряжающий</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButton("charger", this.state.chargerSelectedIndex === 0, 0, "-150px -100px")}
                        {this.renderButton("charger", this.state.chargerSelectedIndex === 1, 1, "-200px -100px")}
                    </div>
                </div>
            </div>
        )
    }
}

function GameButton(props) {
    return (
            <button className={props.class + (props.selected? " selected":"") + " margin-1"}
                    style={{
                        backgroundImage: "url('images/game/actionbtns.svg')",
                        backgroundPosition:  props.position,
                        left: 0,
                        height: 50,
                        width: 50,}}
                    onClick={props.onClick}/>
        )
}

